<template>
  <div class="page_wrapper" v-show="pageMixin_showPage">


    <div class="main_body animated  fadeIn">

      <el-row class="head_area  panel_area">
        <el-col :span="18">
          <span class="title">{{player_type}}信息</span>
          <span class="description">游戏{{player_type}}信息</span>
        </el-col>
        <el-col :span="6">
          <el-button @click="getUserInfo" icon="el-icon-refresh" size="mini" type="primary">刷新</el-button>
          <el-button @click="refreshCache" icon="el-icon-s-opportunity" size="mini" type="primary">更新服务器缓存
          </el-button>

        </el-col>
      </el-row>


      <el-row :gutter="20" class="panel_area">
        <el-col :span="12">

          <el-form :model="userInfo" label-width="180px" ref="form">
            <el-form-item :label="player_type+'ID'">
              {{userInfo.user_id}} <span
              :class="{'font-color-red':userInfo.is_ban==='封号状态','font-color-green':userInfo.is_ban==='未封号'}">({{userInfo.is_ban}})</span>
            </el-form-item>
            <el-form-item :label="player_type+'昵称'">
              {{userInfo.player_name}} <!--            <el-input v-model.trim="userInfo.player_name"></el-input>-->
            </el-form-item>
            <el-form-item label="绑定手机">
              {{userInfo.tel}}
            </el-form-item>
            <el-form-item label="登录微信">
              {{userInfo.openid}}
            </el-form-item>
            <el-form-item label="性别">
              {{userInfo.player_sex}}
            </el-form-item>
            <el-form-item label="Vip等级">
              {{userInfo.vip_lv}}
            </el-form-item>
            <el-form-item label="个性签名">
              {{userInfo.player_sign}}
            </el-form-item>
            <el-form-item label="注册时间">
              {{userInfo.reg_time}}
            </el-form-item>
            <el-form-item label="最近一次登录时间">
              {{userInfo.login_time}}
            </el-form-item>
            <el-form-item label="登录天数">
              {{userInfo.loginDayTotal}}
            </el-form-item>
            <el-form-item label="金币余额">
              {{userInfo.goldcoin}}
            </el-form-item>
            <el-form-item label="钻石余额">
              {{userInfo.diamond}}
            </el-form-item>
            <el-form-item label="奖杯余额">
              {{userInfo.wincup}}
            </el-form-item>
            <el-form-item label="抽奖杯次数">
              {{userInfo.lotteryCupTimes}}
            </el-form-item>
            <el-form-item label="抽奖获得的奖杯总额">
              {{userInfo.all_lottery}}
            </el-form-item>
            <el-form-item label="胜率">
              {{userInfo.winRate}}

            </el-form-item>
            <el-form-item label="充值总额">
              {{userInfo.all_charge}}
            </el-form-item>
            <el-form-item label="兑换金豆花费奖杯总额">
              {{userInfo.convertToGoldCupSum}}
            </el-form-item>
            <el-form-item label="兑换钻石花费奖杯总额">
              {{userInfo.convertToDiamondCupSum}}
            </el-form-item>
            <el-form-item label="赢得金币">
              {{userInfo.winGoldSum}}
            </el-form-item>
            <el-form-item label="赢得钻石">
              {{userInfo.winDiamondSum}}
            </el-form-item>
            <el-form-item label="输掉金币">
              {{userInfo.lostGoldSum}}
            </el-form-item>
            <el-form-item label="输掉钻石">
              {{userInfo.lostDiamondSum}}
            </el-form-item>
            <el-form-item label="金币Balance">
              {{userInfo.goldBalance}}
            </el-form-item>
            <el-form-item label="钻石Balance">
              {{userInfo.diamondBalance}}
            </el-form-item>
            <el-form-item label="总Balance">
              {{userInfo.totalBalance}}
            </el-form-item>

            <el-form-item label="幸运值">
              {{userInfo.lucky_value}}
            </el-form-item>

            <el-form-item label="锁定幸运值">
              <p v-if="userInfo.lucky_lock===1">已锁定</p>
              <p v-else>未锁定</p>
            </el-form-item>

            <el-form-item label="奖杯加成系数">
              {{userInfo.lottery_buff}}
            </el-form-item>


            <el-form-item label="开启奖杯宝箱消耗钻石总和">
              {{userInfo.sum_cost}}
            </el-form-item>

            <el-form-item label="开启奖杯宝箱获得奖杯总和">
              {{userInfo.sum_win}}
            </el-form-item>
            <!--            <el-form-item>-->
            <!--              <el-button @click="onSubmit" type="primary">立即创建</el-button>-->
            <!--              <el-button>取消</el-button>-->
            <!--            </el-form-item>-->
          </el-form>


        </el-col>
        <el-col :span="12">
          <el-card class="options">
            <div class="clearfix" slot="header">
              <span>操作</span>
            </div>
            <div class="options_wrapper">
              <el-button @click="globalMixin_goBack" class="options_item" plain size="small" type="primary"><i
                class="el-icon-back"></i>
              </el-button>
              <el-button @click="getUserInfo" class="options_item" plain size="small" type="primary"><i
                class="el-icon-refresh"></i>
              </el-button>

              <el-popover
                class="options_item"
                placement="top"
                trigger="click"
                v-if="userInfo.is_ai != 0"
                v-model.trim="showPopover.aiScenes"
                width="150">
                <el-select placeholder="请选择AI场次" style="margin-bottom: 15px" v-model="popoverInput.aiScenes">
                  <!--                  封禁AI就是把is_AI改成一个不存在的场次值，比如100-->
                  <el-option label="封禁AI" value="100"></el-option>
                  <el-option label="金币初级场" value="1"></el-option>
                  <el-option label="金币中级场" value="2"></el-option>
                  <el-option label="金币高级场" value="3"></el-option>
                  <el-option label="钻石初级场" value="11"></el-option>
                  <el-option label="钻石中级场" value="12"></el-option>
                  <el-option label="钻石高级场" value="13"></el-option>

                </el-select>

                <el-button @click.native="putDataFn('aiScenes')">确定</el-button>
                <el-button size="small" slot="reference">修改AI场景</el-button>
              </el-popover>


              <el-button @click="globalMixin_jump('RoomHistoryList',{user_id:userInfo.user_id})" plain
                         size="small"
                         type="primary">查看该{{player_type}}游戏记录
              </el-button>
              <template v-if="userInfo.is_ai == 0">
                <el-button @click="putDataFn('deblock')" class="options_item" size="small" type="success"
                           v-if="userInfo.is_ban==='封号状态'">解封{{player_type}}
                </el-button>
                <el-button @click="putDataFn('block')" class="options_item" size="small" type="danger" v-else>
                  封禁{{player_type}}
                </el-button>
              </template>
              <el-popover
                class="options_item"
                placement="top"
                trigger="click"
                v-model.trim="showPopover.gold"
                width="150">
                <el-input
                  clearable
                  placeholder="请输入金币数量"
                  style="margin-bottom: 15px"
                  v-model.trim="popoverInput.gold"
                >
                </el-input>
                <el-button @click.native="putDataFn('gold')">确定</el-button>
                <el-button size="small" slot="reference">发放金币</el-button>
              </el-popover>
              <el-popover
                class="options_item"
                placement="top"
                trigger="click"
                v-model.trim="showPopover.diamond"
                width="150">
                <el-input
                  clearable
                  placeholder="请输入钻石数量"
                  style="margin-bottom: 15px"
                  v-model.trim="popoverInput.diamond"
                >
                </el-input>
                <el-button @click.native="putDataFn('diamond')">确定</el-button>
                <el-button size="small" slot="reference">发放钻石</el-button>
              </el-popover>
              <el-popover
                class="options_item"
                placement="top"
                trigger="click"
                v-model.trim="showPopover.trophy"
                width="150">
                <el-input
                  clearable
                  placeholder="请输入奖杯数量"
                  style="margin-bottom: 15px"
                  v-model.trim="popoverInput.trophy"
                >
                </el-input>
                <el-button @click.native="putDataFn('trophy')">确定</el-button>
                <el-button size="small" slot="reference">发放奖杯</el-button>
              </el-popover>

              <el-popover
                class="options_item"
                placement="top"
                trigger="click"
                v-model.trim="showPopover.trophyCoefficient"
                width="150">
                <el-input
                  clearable
                  placeholder="范围:0.01-100"
                  style="margin-bottom: 15px"
                  v-model.trim="popoverInput.trophyCoefficient"
                >
                </el-input>
                <el-button @click.native="putDataFn('trophyCoefficient')">确定</el-button>
                <el-button size="small" slot="reference">修改奖杯加成系数</el-button>
              </el-popover>

              <el-popover
                class="options_item"
                placement="top"
                trigger="click"
                v-model.trim="showPopover.luckyValue"
                width="150">
                <el-input
                  clearable
                  placeholder="范围:0-10"
                  style="margin-bottom: 15px"
                  v-model.trim="popoverInput.luckyValue"
                >
                </el-input>
                <el-switch active-text="锁定"
                           inactive-text="未锁定"
                           style="margin-bottom: 5px"
                           v-model="lockLuckValue">
                </el-switch>
                <el-button @click.native="putDataFn('luckyValue')">确定</el-button>
                <el-button size="small" slot="reference">修改{{player_type}}幸运值<i class="el-icon-lock font-color-red"
                                                                                v-if="lockLuckValue"></i>
                  <i class="el-icon-unlock font-color-green" v-else></i>

                </el-button>
              </el-popover>
            </div>


            <!--            <el-button plain type="primary">发放金币</el-button>-->
            <!--            <el-button plain type="primary">发放钻石</el-button>-->
            <!--            <el-button plain type="primary">发放奖杯</el-button>-->


          </el-card>

        </el-col>


      </el-row>


    </div>

  </div>
</template>

<script>
    import pageMixin from "@/mixin/pageMixin";
    import tableMixin from "@/mixin/tableMixin";
    import TableToolsBox from "../childComponents/TableToolsBox";

    export default {
        name: "PlayInfo",
        components: {
            TableToolsBox,
        },
        mixins: [pageMixin, tableMixin],
        props: {},
        data() {
            return {
                user_id: 0,
                showPopover: {
                    aiScenes: false,
                    gold: false,
                    diamond: false,
                    trophy: false,
                    trophyCoefficient: false,//奖杯加成系数
                    luckyValue: false
                },//用于是否显示 弹出层
                popoverInput: {
                    aiScenes: '',
                    gold: '',
                    diamond: '',
                    trophy: '',
                    trophyCoefficient: '',//奖杯加成系数
                    luckyValue: ''//幸运值
                },
                lockLuckValue: false,//锁定幸运值
                putGold: "",
                userInfo: {
                    user_id: 0,
                    player_name: "",
                    player_portrait: "",
                    player_sex: "",
                    player_sign: "",
                    goldcoin: 0,
                    diamond: 0,
                    wincup: "",
                    play_times: 0,
                    win_times: 0,
                    tel: null,
                    reg_time: "",
                    openid: "",
                    is_ai: 0,
                    all_charge: "",
                    login_time: "",
                    is_ban: "",
                    lotteryCupTimes: 0,
                    lotteryCupSum: 0,
                    loginDayTotal: 0,
                    winRate: 0,
                    convertToDiamondCupSum: 0,
                    convertToGoldCupSum: 0,
                    winGoldSum: 0,
                    lostGoldSum: 0,
                    winDiamondSum: 0,
                    lostDiamondSum: 0,
                    goldBalance: 0,
                    diamondBalance: 0,
                    totalBalance: 0,
                    lottery_buff: null,
                    lucky_lock: 0,
                    lucky_value: 0,
                },
                form: {
                    name: '',
                    region: '',
                    date1: '',
                    date2: '',
                    delivery: false,
                    type: [],
                    resource: '',
                    desc: ''
                }

            }
        },
        watch: {},
        computed: {
            player_type: function () {
                return this.userInfo.is_ai === 0 ? '用户' : 'AI';
            }
        },
        methods: {
            getUserInfo() {

                this.globalMixin_request('/player/read', "get",
                    {
                        user_id: this.user_id,
                    }).then(res => {
                    {
                        const {code, data, message} = res;

                        if (code === 10000) {
                            this.userInfo = data[0];
                            this.popoverInput.trophyCoefficient = this.userInfo.lottery_buff;// 同步 奖杯系数加成

                            this.popoverInput.luckyValue = this.userInfo.lucky_value// 同步幸运值
                            this.userInfo.lucky_lock === 1 ? this.lockLuckValue = true : this.lockLuckValue = false;//同步锁定状态

                            this.popoverInput.aiScenes = this.userInfo.is_ai.toString();
                        }
                    }
                })
            },
            refreshCache() {
                this.globalMixin_request('/player_refresh?user_id=' + this.user_id).then(res => {
                        const {code, data, message} = res;
                        if (code === 10000) {
                            this.ele_notify("更新缓存成功", 'success');
                        }
                    }
                );
            },
            putDataFn(type) {
                this.showPopover[type] = false;
                const value = this.popoverInput[type];

                if (type === 'deblock' || type === 'block') {
                    let tips = "";
                    if (type === 'deblock') {
                        tips = "确定解封该用户吗？"
                    } else if (type === 'block') {
                        tips = "确定封禁该用户吗？"
                    }
                    this.$confirm(tips, '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.globalMixin_request('/player/update', 'put', {
                            id: this.user_id,
                            operation_type: type,
                            value,
                        }).then(res => {
                            this.$message({
                                type: 'success',
                                message: res.message,
                            });

                            this.popoverInput[type] = '';//清空输入框
                            this.getUserInfo()

                        })

                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消'
                        });
                    });
                } else {
                    this.globalMixin_request('/player/update', 'put', {
                        id: this.user_id,
                        operation_type: type,
                        lockLuckValue: this.lockLuckValue ? 1 : 0,//锁定幸运值 0未锁定,1锁定
                        value,
                    }).then(res => {
                        const {code, data, message} = res;
                        if (code === 10000) {
                            this.ele_notify(message, 'success');
                            this.refreshCache();
                        }

                        this.getUserInfo()

                    })
                }

            }
        },
        created() {

            this.user_id = this.$route.query.user_id || 0;
            if (!this.user_id) {
                this.$alert('参数有误', '错误', {
                    confirmButtonText: '确定',
                    callback: action => {
                        this.globalMixin_goBack();
                    }
                });

                return;
            }


            if (this.user_id) {
                this.getUserInfo()
            }
        },
        mounted() {
        },
    }

</script>

<style lang='scss' scoped>
  @import "../../assets/styles/base";

  .table-expand {
    font-size: 0;
  }

  .table-expand label {
    width: 90px;
    color: #99a9bf;
  }

  .table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }

  .page_wrapper {

    .options {
      position: fixed;
      width: 500px;

      .options_wrapper {

        .options_item {
          margin-bottom: 10px;
        }

      }

    }
  }
</style>



